import React from 'react';
import { SearchBar } from 'antd-mobile';

class Search extends React.Component {
  state = {
    value: '',
  };
 
  onSubmit= (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  handleClick = () => {
    this.manualFocusInst.focus();
  }
  render() {
    return (
      <SearchBar
        placeholder=""
        onSubmit={this.onSubmit}
        cancelText={''}        
      />
    );
  }
}

export default Search;